<template>
  <div class="page">
    <div class="content">
      <div class="gray pl20 f14 pt15 pb5">服务内容</div>
      <div class="bgc-white clearfix pt15 pb15">
        <div class="service-item" :class="{'full': it.k == 1}" v-for="(it, itkey) in content" :key="itkey">{{ it.v }}
        </div>
      </div>
      <div class="gray pl20 f14 pt15 pb5">服务方式</div>
      <div class="bgc-white clearfix pt15 pb15">
        <div class="service-item" :class="{'full': it.k == 1}" v-for="(it, itkey) in way" :key="itkey">{{ it.v }}</div>
      </div>
    </div>
    <div class="btn-wrap">
      <router-link class="btn btn-success f17 btn-round" :to="{name: 'apply', params: {serviceType, tag}}">
        申请服务
      </router-link>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  const lawdata1 = [{
    k: 0,
    v: '法律顾问'
  }, {
    k: 0,
    v: '诉讼代理'
  }, {
    k: 0,
    v: '诉讼融资'
  }, {
    k: 0,
    v: '劳动用工'
  }, {
    k: 0,
    v: '融资并购'
  }, {
    k: 0,
    v: '欠款催收'
  }, {
    k: 0,
    v: '侵权维权'
  }, {
    k: 0,
    v: '股权筹划'
  }, {
    k: 0,
    v: '合同管理'
  }]
  const lawdata2 = [{
    k: 1,
    v: '微信群+线上一对一服务'
  }, {
    k: 1,
    v: '定制企业法律小课堂'
  }, {
    k: 1,
    v: '上门诊断服务'
  }, {
    k: 1,
    v: '每个项目可配备2-3名律师全天候服务'
  }, {
    k: 1,
    v: '重大事宜出局书面法律意见'
  }, {
    k: 1,
    v: '互联网+热线服务'
  }]
  const itdata1 = [{
    k: 0,
    v: '计算机硬件设备维护'
  }, {
    k: 0,
    v: '网络日常维护'
  }, {
    k: 0,
    v: '应用软件维护'
  }, {
    k: 0,
    v: '系统维护'
  }, {
    k: 0,
    v: '办公外设维护'
  }, {
    k: 0,
    v: '病毒防护'
  }, {
    k: 1,
    v: '企业IT资产管理'
  }, {
    k: 1,
    v: '企业IT顾问咨询服务'
  }]
  const itdata2 = [{
    k: 0,
    v: '小程序一键叫修'
  }, {
    k: 0,
    v: '应急上门服务'
  }, {
    k: 0,
    v: '企业IT咨询报告'
  }, {
    k: 0,
    v: '远程技术支持'
  }, {
    k: 1,
    v: '定期巡检服务'
  }, {
    k: 1,
    v: '全国连锁企业本地交付'
  }]
  export default {
    name: 'itService',
    data() {
      return {
        serviceType: 1,
        content: [],
        way: []
      }
    },
    created() {
      this.setServiceType()
    },
    methods: {
      checkServiceType(type) {
        if (type == 1) {
          document.title = 'IT服务'
          this.content = itdata1
          this.way = itdata2
        } else if (type == 2) {
          document.title = '法律服务'
          this.content = lawdata1
          this.way = lawdata2
        }
      },
      setServiceType() {
        if (utils.isEmpty(this.$route.params)) {
          this.$router.push({
            name: 'Index'
          })
        } else {
          let {serviceType, tag} = this.$route.params
          this.serviceType = serviceType
          this.tag = tag
          this.checkServiceType(serviceType)
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import '../../../assets/scss/Common/variables.scss';

  .service-item {
    font-size: rem(14px);
    &.full {
      width: 100vw;
    }
    position: relative;
    width: 50vw;
    float: left;
    padding: rem(7px) rem(5px) rem(7px) rem(44px);
    &:before {
      content: '';
      position: absolute;
      top: rem(12px);
      left: rem(24px);
      width: rem(10px);
      height: rem(10px);
      border-radius: 50%;
      background-color: $themes-color;
    }
  }
</style>
